<template>
  <div>
    <div class="title">
      <span class="iconfont icon-left gobackIcon" @click="()=>{this.$router.go(-1)}"></span>
      <p>修改简介</p>
    </div>
    <div class="content">
      <div class="centerValue" v-if="userInfo">
        <ul>
          <li>
            <p>商家简介</p>
            <textarea rows="4" disabled v-model="userInfo.description" placeholder="显示原简介"></textarea>
          </li>
          <li>
            <p>修改简介</p>
            <textarea rows="4"  v-model="newDescription" placeholder="请输入修改简介"></textarea>
          </li>
        </ul>
      </div>
      <div class="footBtn">
        <van-button block class="btn" @click="changeIntorduction()">确认修改</van-button>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data: () => ({
    userInfo: null,
    newDescription: null
  }),
  created() {
    this.$axios.get(`ym/mall-online-merchant/info`).then(res => {
      if (res.code === 0) {
        this.userInfo = res.data;
        console.log(this.userInfo);
      } else {
        console.log("获取商家信息失败");
      }
    });
  },
  methods: {
    changeIntorduction() {
      let event = {
        mobile: this.userInfo.mobile,
        storeName: this.userInfo.storeName,
        description: this.newDescription,
        wechat: this.userInfo.wechat,
        alipay: this.userInfo.alipay,
        idcardImgFront: this.userInfo.idcardImgFront,
        idcardImgBack: this.userInfo.idcardImgBack,
        businessLicenseNo: this.userInfo.businessLicenseNo,
        businessLicenseImg: this.userInfo.businessLicenseImg
      };
      console.log(event);
      this.$axios.post(`ym/mall-online-merchant/enter`, event).then(res => {
        console.log(res);
        if (res.data.code === 0) {
          this.$Toast("修改成功");
          this.$router.push('/shop');
        } else {
          this.$Toast("修改失败");
        }
      });
    }
  }
};
</script>
